@import ../../variable
#activerepo
    justify-content: start
    max-width: 1280px
    margin: 0 auto
    header
        text-align: left
        width: 100%
    #activerepo-container
        width: 100% !important
        min-width: 100%
        padding: 0
        header
            padding-bottom: 1.5rem
            margin: 0
            h5
                text-transform: none
        figure
        time, a
            text-transform: capitalize
            color: $color-primary
            opacity: .8
            &:hover
                opacity: 1

        .article-text
            margin-top: 1rem !important
            .article-text-header
                padding: 1rem 0
                display: flex
                flex-direction: column
                gap: .5rem !important
                time
                    text-transform: capitalize
                    font-size: 1.1rem
                    padding: 1rem
                a
                    text-transform: capitalize !important
                    padding: 1.5rem 0 !important         

        p
            font-size: 1.2rem !important
            text-transform: none
            line-height: 1.5rem
            padding-top: 2rem
        h3
            color: $color-light2
            background: $color-dark
            // padding-top: 2rem
            margin-top: 1rem
            padding: 1rem .5rem
        ul
            // background: gold
            text-transform: none
            padding-left: 2rem
            padding-top: .5rem
            li
                list-style: bullet
                text-transform: none
                padding: .2rem 0
