@import ../../variable
    
    
menu
    min-height: 600px
    height: 100vh
    width: 500px
    display: flex
    flex-direction: column
    justify-content: space-between
    border-left: 5px solid $color-primary
    // background-color: rgb(255, 210, 210)
    background-color: $color-light2
    // background-image: $color-bg-gradient
    position: fixed
    top: 0
    overflow-y: auto 
    z-index: 44
    left: 0px
    header
        width: 100%
        height: 250px
        display: flex
        flex-direction: column
        justify-content: flex-end
        position: relative
        padding: 1rem
        h2, h4
            position: relative
            z-index: 3
            color: $color-light
        #menu-profile-img
            position: absolute
            left: 0
            top: 0
            opacity: .9
    section
        ul
            li
                padding: 1rem
                font-size: 1.2rem
                @include l-space(.5, $color-primary, 1)
    footer
        a
            padding: .5rem
            .icon
                width: 1.5rem
                height: 1.5rem
                transition: .3s !important
            .icon:hover
                fill: $color-primary
.active
    @include l-space-active(.5, $color-primary, 1)
    



@media screen and (max-width: 1280px) 
    menu
        max-width: 100%
        left: -500px
